import React from "react";
import { Route, Switch } from "react-router-dom";
import Home from "./components/home";
import TextCompressionContainer from "./components/text-compression";

const Routes = () => {
  return (
    <Switch>
      <Route exact path='/' render={Home} />

      {/* Text Operations */}
      <Route exact path='/text/compression' render={TextCompressionContainer} />
      <Route exact path='/text/encryption' render={TextCompressionContainer} />
      <Route exact path='/text/hybrid' render={TextCompressionContainer} />

      {/* File Operations */}
      <Route exact path='/file/compression' render={TextCompressionContainer} />
      <Route exact path='/file/encryption' render={TextCompressionContainer} />
      <Route exact path='/file/hybrid' render={TextCompressionContainer} />

      {/* 404 Not Found */}
      <Route exact path='/*' render={TextCompressionContainer} />
    </Switch>
  );
};

export default Routes;
