import Axios from "axios";
import React, { useState, useEffect } from "react";

function Home() {
  const [homedata, sethomedata] = useState("");

  useEffect(() => {
    Axios.get("/api/home").then((res) => {
      console.log(res);
      sethomedata(res.data);
    });
  }, []);
  return homedata !== "" ? <h2>{homedata}</h2> : "Loading ...";
}

export default Home;
